.ctk-date-time-picker{width:100%;margin:0 auto;text-align:left;font-size:14px;border-radius:4px;position:relative
}
.ctk-date-time-picker *{box-sizing:border-box
}
.ctk-date-time-picker .time-picker-overlay{z-index:2;position:fixed;top:0;left:0;right:0;bottom:0
}
