}
.ctk-date-time-picker .field{position:relative
}
.ctk-date-time-picker .field .field-label{position:absolute;top:3px;cursor:pointer;left:13px;-webkit-transform:translateY(25%);transform:translateY(25%);opacity:0;transition:all .25s cubic-bezier(0.645, 0.045, 0.355, 1);font-size:11px;color:rgba(0,0,0,.54)
}
.ctk-date-time-picker .field .field-input{cursor:pointer;background-color:#fff;transition-duration:.3s;position:relative;width:100%;height:42px;min-height:42px;padding:0 12px;font-weight:300;-webkit-appearance:none;outline:none;border:1px solid rgba(0,0,0,.2);border-radius:4px;font-size:14px;z-index:0
