}
.ctk-date-time-picker .field.has-value .field-label{opacity:1;-webkit-transform:translateY(0);transform:translateY(0);font-size:11px
}
.ctk-date-time-picker .field.has-value .field-input{padding-top:14px
}
.ctk-date-time-picker .field.is-focused .field-input{border-color:#1e90ff
}
