}
.ctk-date-time-picker .field.is-disabled .field-input{border-color:#ccc;background:#f2f2f2
}
.ctk-date-time-picker .field.is-disabled .field-label,.ctk-date-time-picker .field.is-disabled .field-input{cursor:default
}
.ctk-date-time-picker .text-danger{color:#ff4500 !important
}
.ctk-date-time-picker.is-dark .field-label{color:#ffffffb3
